/* Основные переменные цсс. */

// В colors помещаем цвета
/* colors start */
$withOutColor: transparent;
$whiteColor: #fff;
$lightGrey: #efefef;
$darkGrey: #a9a9a9;
$black: #001a33;
$primary: #1976d2;
$secondary: #fe6b8b;
$lightBlue: #cbdfe0;
$red: red;
$green: green;
$orange: #ef9401;
$columnBg: #f4f5f7;
$scrollBarItemThumb: #c4c4c4;
$scrollBarItemBg: #d9d9d9;
$pageGradient: linear-gradient(45deg,#f8a078,#6dc6f0);
$boxShadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
/* colors end */

//В values основные значения
/* values start */
$wrapper-max-width: 1152px;
$transitionTime: 0.3s;
$borderRadiusMin: 10px;
$borderRadiusBig: 28px;
$pageSumTitleText: 28px;
$pageTitleText: 40px;
$oneBoardPageOffset: max(calc(calc(100vw - $wrapper-max-width) / 2), 20px);
$textSize: 20px;
$headTaskSize: 40px;
/* values end */

/* common parts styles start */
%commonHeadItemStyles {
  box-shadow: $boxShadow;
  border-radius: $borderRadiusBig;
  color: $black;
  background: $whiteColor;
}
/* common parts styles end */
